@use 'variables' as v;

h1, h2, h3 {
    font-family: v.$fuente_principal;
    margin: 0 0 calc(v.$separacion / 2) 0 ;
    font-weight: 900;
    color: v.$blanco;
    text-align: center;
}

h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
    text-align: center;
}
p {
    color: v.$blanco;
    font-size: 2rem;
}

.titulo-login {
    margin: 5rem 0;
    font-weight: v.$bold;
    color: transparent;
    background: linear-gradient(to right, #ff0000, #ffff00);
    -webkit-background-clip: text;

}

.text-center{
    text-align: center;
}